#contact {
  background-color: #333;
  color: white;
  padding: 50px 15px;

  .contact-title {
    margin-bottom: 45px;
    font-size: 24px;
  }
  .list {
    list-style: none;
    display: flex;
    flex-direction: column;

    &-item {
      display: flex;
      align-items: center;
      margin-bottom: 25px;
      &-icon {
        margin-right: 25px;
      }
    }
  }
}
